#mainDiv{
  align-items: center;
  justify-content: center;
  margin:0;
  padding:0;
  width:511px;
  display: inline-block;
  background-color:#292929;
  background-image: url('https://cf.geekdo-images.com/opengraph/img/iTZcvNfBptXqWcMFkLPwvOjeRxA=/fit-in/1200x630/pic2419375.jpg');
  font-family: 'Montserrat', sans-serif;
}
#wrapper{
  width:511px;
  min-height:630px;
  box-sizing:border-box;
  display:flex;
  align-items:center;
  justify-content:center;
}
.my-super-cool-btn{
  margin-top: 100px;
  position:relative;
  text-decoration:none;
  color:#000;
  text-shadow: white 0px 0px 10px;
  letter-spacing:1px;
  font-size:2rem;
  box-sizing:border-box;
}
.my-super-cool-btn span{
  position:relative;
  box-sizing:border-box;
  display:flex;
  align-items:center;
  justify-content:center;
  width:200px;
  height:200px;
}
.my-super-cool-btn span:before{
  content:'';
  width:100%;
  height:100%;
  display:block;
  position:absolute;
  border-radius:100%;
  border:7px solid #3386FF;
  box-sizing:border-box;
  -webkit-transition: all .85s cubic-bezier(0.25, 1, 0.33, 1);
  transition: all .85s cubic-bezier(0.25, 1, 0.33, 1);
  box-shadow: 0 30px 85px rgba(0,0,0,0.14), 0 15px 35px rgba(0,0,0,0.14);
}
.my-super-cool-btn:hover span:before{
  -webkit-transform:scale(0.8);
          transform:scale(0.8);
  box-shadow: 0 20px 55px rgba(0,0,0,0.14), 0 15px 35px rgba(0,0,0,0.14);
}
span {
  text-align: center;

  background-image: url('https://i.imgur.com/NYFd64r.gif');
}
.my-super-cool-btn .dot1s-container{
  opacity:0;
  -webkit-animation: intro 1.6s;
          animation: intro 1.6s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.my-super-cool-btn .dot1{
  width:8px;
  height:8px;
  display:block;
  background-color:#3386FF;
  border-radius:100%;
  position:absolute;
  -webkit-transition: all .85s cubic-bezier(0.25, 1, 0.33, 1);
  transition: all .85s cubic-bezier(0.25, 1, 0.33, 1);
}
.my-super-cool-btn .dot1:nth-child(1){
  top:0px;
  left:0px;
  -webkit-transform:rotate(-140deg);
          transform:rotate(-140deg);
  -webkit-animation: swag1-out 0.3s;
          animation: swag1-out 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  opacity:0;
}
.my-super-cool-btn .dot1:nth-child(2){
  top:0px;
  right:0px;
  -webkit-transform:rotate(140deg);
          transform:rotate(140deg);
  -webkit-animation: swag2-out 0.3s;
          animation: swag2-out 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  opacity:0;
}
.my-super-cool-btn .dot1:nth-child(3){
  bottom:0px;
  left:0px;
  -webkit-transform:rotate(140deg);
          transform:rotate(140deg);
  -webkit-animation: swag3-out 0.3s;
          animation: swag3-out 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  opacity:0;
}
.my-super-cool-btn .dot1:nth-child(4){
  bottom:0px;
  right:0px;
  -webkit-transform:rotate(-140deg);
          transform:rotate(-140deg);
  -webkit-animation: swag4-out 0.3s;
          animation: swag4-out 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  opacity:0;
}
.my-super-cool-btn:hover .dot1:nth-child(1){
  -webkit-animation: swag1 0.3s;
          animation: swag1 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.my-super-cool-btn:hover .dot1:nth-child(2){
  -webkit-animation: swag2 0.3s;
          animation: swag2 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.my-super-cool-btn:hover .dot1:nth-child(3){
  -webkit-animation: swag3 0.3s;
          animation: swag3 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.my-super-cool-btn:hover .dot1:nth-child(4){
  -webkit-animation: swag4 0.3s;
          animation: swag4 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes intro {
   0% {
     opacity:0;
  }
  100% {
     opacity:1;
  }
}
@keyframes intro {
   0% {
     opacity:0;
  }
  100% {
     opacity:1;
  }
}
@-webkit-keyframes swag1 {
   0% {
     top:50px;
